body {
    box-sizing: border-box;
    margin:  0;
    padding: 0;
    background-color: aqua;
}
.center_area {
    padding: .5rem 0;
    width: 400px;
    height:97.8vh;
    margin: auto;
    text-align: center;
    border-radius: 1rem;
    background-color: rgb(158, 221, 76);
}
.main_heading {
    /* margin: 1rem; */
    color: rgb(243, 174, 240);
    background-color: rgb(48, 48, 122);
    border-radius: .5rem;
    padding: .6rem 0rem;
}
.task {

}
input {
    border: none;
    width: 270px;
    padding: 0px .4rem;
    font-size: 2rem;
    border-bottom: 6px solid rgb(0, 0, 0);
    border-radius: .4rem;
    background: transparent;
}
::placeholder {
    color: red;
    opacity: 1; 
  }
  
.add {
    padding: 0rem .6rem;
    font-size: 2.2rem;
    background-color: teal;
    width: 100px;
    margin-left: 1rem;
    margin-right: .2rem;
    border-radius: 1.35rem;
}
.add:hover {
    color: white;
    padding: .2rem .8rem;
    margin-left: .8rem;
    margin-right: 0;
    background-color: rgb(4, 36, 36);
}
li {
    list-style: none;
    display: block;
}
ul {
    padding-left: 1rem;
}
.delete_task {
    display: flex;
    text-align: left;
    margin-bottom: .4rem;
    font-size: 2rem;
}
.delete {
    padding: .3rem .6rem;
    border-radius: 1.3rem;
    font-size: 1.3rem;
    margin: .4rem 1rem 0 .6rem;
    background-color: rgba(255, 0, 0, 0.37);
}
.delete:hover {
    padding: .5rem .8rem;
    margin-right: .6rem;
    background-color: rgba(197, 12, 12, 0.911);
}